"use client"

import { useState } from "react"
import type { CRMRecord, ProtectRecord, SettlementRecord, Status, Remark } from "@/lib/types"
import { useCRMStore } from "@/lib/store"
import { cn } from "@/lib/utils"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import {
  User,
  Phone,
  Building,
  Calendar,
  FileText,
  MessageSquare,
  History,
  Plus,
  Edit2,
  Trash2,
  Save,
  X,
} from "lucide-react"

const statusStyles: Record<Status, string> = {
  new: "bg-blue-500/10 text-blue-500 border-blue-500/20",
  "in-progress": "bg-yellow-500/10 text-yellow-500 border-yellow-500/20",
  "follow-up": "bg-purple-500/10 text-purple-500 border-purple-500/20",
  closed: "bg-green-500/10 text-green-500 border-green-500/20",
}

const stages = ["Part Payment", "Skip EMI"]

export function RecordDetailModal({ recordId, open, onClose }: { recordId: string | null; open: boolean; onClose: () => void }) {
  const { records, updateRecordStatus, updateRecordStage, updatePartPayment, addRemark, updateRemark, deleteRemark, deleteActivityLog } = useCRMStore()
  const [newRemark, setNewRemark] = useState("")
  const [editingRemarkId, setEditingRemarkId] = useState<string | null>(null)
  const [editingRemarkText, setEditingRemarkText] = useState("")

  const record = records.find((r) => r.id === recordId) || null

  if (!record) return null

  const isProtect = record.type === "protect"
  const protectRecord = record as ProtectRecord
  const settlementRecord = record as SettlementRecord

  const handleAddRemark = () => {
    if (!newRemark.trim()) return
    addRemark(record.id, {
      text: newRemark,
      createdAt: new Date().toISOString(),
      createdBy: "Admin",
    })
    setNewRemark("")
  }

  const handleUpdateRemark = (remarkId: string) => {
    if (!editingRemarkText.trim()) return
    updateRemark(record.id, remarkId, editingRemarkText)
    setEditingRemarkId(null)
    setEditingRemarkText("")
  }

  const handleDeleteRemark = (remarkId: string) => {
    deleteRemark(record.id, remarkId)
  }

  const startEditRemark = (remark: Remark) => {
    setEditingRemarkId(remark.id)
    setEditingRemarkText(remark.text)
  }

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-3xl bg-card border-border max-h-[90vh] flex flex-col p-0 gap-0">
        {/* Header - Fixed */}
        <div className="p-6 border-b border-border bg-card z-10">
          <DialogHeader>
            <div className="flex items-start justify-between">
              <div className="flex items-center gap-4">
                <div className="flex h-12 w-12 items-center justify-center rounded-full bg-primary/20">
                  <span className="text-lg font-semibold text-primary">
                    {record.name
                      .split(" ")
                      .map((n) => n[0])
                      .join("")
                      .substring(0, 2)}
                  </span>
                </div>
                <div>
                  <DialogTitle className="text-xl font-semibold text-foreground">{record.name}</DialogTitle>
                  <div className="flex items-center gap-2 mt-1">
                    <Badge
                      variant="outline"
                      className={
                        isProtect
                          ? "bg-blue-500/10 text-blue-500 border-blue-500/20 text-xs"
                          : "bg-green-500/10 text-green-500 border-green-500/20 text-xs"
                      }
                    >
                      {record.type}
                    </Badge>
                    <Badge variant="outline" className="text-xs capitalize">
                      {record.partner}
                    </Badge>
                  </div>
                </div>
              </div>
            </div>
          </DialogHeader>

          {/* Status & Stage Controls */}
          <div className="flex items-center gap-4 mt-6">
            <div className="flex items-center gap-2">
              <span className="text-sm text-muted-foreground w-12">Status:</span>
              <Select value={record.status} onValueChange={(v) => updateRecordStatus(record.id, v as Status)}>
                <SelectTrigger className="w-[140px] h-8 text-sm">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="new">New</SelectItem>
                  <SelectItem value="in-progress">In Progress</SelectItem>
                  <SelectItem value="follow-up">Follow-up</SelectItem>
                  <SelectItem value="closed">Closed</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="flex items-center gap-2">
              <span className="text-sm text-muted-foreground w-12">Stage:</span>
              <Select value={record.stage} onValueChange={(v) => updateRecordStage(record.id, v)}>
                <SelectTrigger className="w-[160px] h-8 text-sm">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {stages.map((stage) => (
                    <SelectItem key={stage} value={stage}>
                      {stage}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Part Payment Input (Only if Part Payment stage is selected or just generally available?) -> User asked for "Part Payment column", implying it's a field. Let's put it near stage. */}
          <div className="flex items-center gap-4 mt-4 bg-muted/30 p-3 rounded-md border border-border/50">
            <span className="text-sm font-medium w-32">Part Payment (â‚¹):</span>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-muted-foreground text-sm">â‚¹</span>
              <input
                type="number"
                className="h-9 w-32 rounded-md border border-input bg-background pl-7 pr-3 text-sm ring-offset-background focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
                placeholder="0"
                value={record.partPaymentAmount || ""}
                onChange={(e) => updatePartPayment(record.id, Number(e.target.value))}
              />
            </div>
          </div>
        </div>

        {/* Scrollable Content */}
        <div className="flex-1 overflow-y-auto p-6 space-y-8">

          {/* Section: Details */}
          <section className="space-y-4">
            <div className="flex items-center gap-2 text-primary/80 font-medium pb-2 border-b border-border/50">
              <FileText className="h-4 w-4" />
              <h3>Details</h3>
            </div>

            <div className="grid grid-cols-2 gap-4">
              {/* Contact Info */}
              <Card className="bg-muted/20 border-border shadow-none">
                <CardContent className="pt-4 space-y-2">
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-muted-foreground">Mobile</span>
                    <span className="text-sm font-mono">{record.mobileNumber}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-muted-foreground">Partner</span>
                    <span className="text-sm capitalize">{record.partner}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-muted-foreground">Form Filled</span>
                    <span className="text-sm">{record.formFilledDate}</span>
                  </div>
                </CardContent>
              </Card>

              {/* Upload Info */}
              <Card className="bg-muted/20 border-border shadow-none">
                <CardContent className="pt-4 space-y-2">
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-muted-foreground">Source</span>
                    <span className="text-sm truncate max-w-[120px]" title={record.uploadedFrom}>{record.uploadedFrom}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-muted-foreground">Uploaded</span>
                    <span className="text-sm">{new Date(record.uploadedAt).toLocaleDateString()}</span>
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Specific Details */}
            <Card className="bg-muted/20 border-border shadow-none">
              <CardContent className="pt-4">
                {isProtect ? (
                  <div className="grid grid-cols-3 gap-y-4 gap-x-8">
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Plan</p>
                      <p className="text-sm font-medium">{protectRecord.plan}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Institution</p>
                      <p className="text-sm font-medium">{protectRecord.institution}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Account No</p>
                      <p className="text-sm font-medium font-mono">{protectRecord.accountNumber}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">EMI Amount</p>
                      <p className="text-sm font-medium font-mono">â‚¹{protectRecord.emiAmount.toLocaleString()}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">EMI Date</p>
                      <p className="text-sm font-medium">{protectRecord.emiDate}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Current DPD</p>
                      <p className="text-sm font-medium">{protectRecord.currentDpd} days</p>
                    </div>
                  </div>
                ) : (
                  <div className="grid grid-cols-3 gap-y-4 gap-x-8">
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Lender</p>
                      <p className="text-sm font-medium">{settlementRecord.lenderName}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Loan Acc No</p>
                      <p className="text-sm font-medium font-mono">{settlementRecord.loanAccNo}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Due Amount</p>
                      <p className="text-sm font-medium font-mono">â‚¹{settlementRecord.dueAmt.toLocaleString()}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Rec. Amount</p>
                      <p className="text-sm font-medium font-mono text-green-500">â‚¹{settlementRecord.recommendedAmt.toLocaleString()}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Legal Notice</p>
                      <Badge variant={settlementRecord.isLegalNotice ? "destructive" : "outline"} className="text-[10px] h-5">
                        {settlementRecord.isLegalNotice ? "Yes" : "No"}
                      </Badge>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Employment</p>
                      <p className="text-sm font-medium">{settlementRecord.currEmployment}</p>
                    </div>
                  </div>
                )}
              </CardContent>
            </Card>
          </section>

          {/* Section: Remarks */}
          <section className="space-y-4">
            <div className="flex items-center gap-2 text-primary/80 font-medium pb-2 border-b border-border/50">
              <MessageSquare className="h-4 w-4" />
              <h3>Remarks</h3>
            </div>

            <div className="bg-muted/20 border border-border rounded-lg p-4 space-y-4">
              <div className="space-y-3 max-h-[300px] overflow-y-auto pr-2 custom-scrollbar">
                {record.remarks.length === 0 ? (
                  <div className="text-center py-8 text-muted-foreground text-sm italic">
                    No remarks yet. Add one below.
                  </div>
                ) : (
                  record.remarks.map((remark) => (
                    <div key={remark.id} className="bg-background rounded-md p-3 border border-border/50 shadow-sm">
                      {editingRemarkId === remark.id ? (
                        <div className="space-y-2">
                          <Textarea
                            value={editingRemarkText}
                            onChange={(e) => setEditingRemarkText(e.target.value)}
                            className="min-h-[60px] text-sm"
                          />
                          <div className="flex justify-end gap-2">
                            <Button size="sm" variant="ghost" onClick={() => setEditingRemarkId(null)} className="h-7 text-xs">
                              Cancel
                            </Button>
                            <Button size="sm" onClick={() => handleUpdateRemark(remark.id)} className="h-7 text-xs">
                              Save
                            </Button>
                          </div>
                        </div>
                      ) : (
                        <div>
                          <p className="text-sm text-foreground whitespace-pre-wrap break-words leading-relaxed">{remark.text}</p>
                          <div className="flex items-center justify-between mt-2 pt-2 border-t border-dashed border-border/50">
                            <span className="text-[10px] text-muted-foreground font-medium">
                              {remark.createdBy} â€¢ {new Date(remark.createdAt).toLocaleString()}
                              {remark.updatedAt && " (edited)"}
                            </span>
                            <div className="flex">
                              <Button
                                size="icon"
                                variant="ghost"
                                className="h-5 w-5 hover:bg-muted"
                                onClick={() => startEditRemark(remark)}
                              >
                                <Edit2 className="h-3 w-3 text-muted-foreground" />
                              </Button>
                              <Button
                                size="icon"
                                variant="ghost"
                                className="h-5 w-5 hover:bg-destructive/10 hover:text-destructive"
                                onClick={() => handleDeleteRemark(remark.id)}
                              >
                                <Trash2 className="h-3 w-3" />
                              </Button>
                            </div>
                          </div>
                        </div>
                      )}
                    </div>
                  ))
                )}
              </div>

              {/* Add Remark Input */}
              <div className="flex flex-col gap-2 pt-2">
                <Textarea
                  placeholder="Type a new remark here..."
                  value={newRemark}
                  onChange={(e) => setNewRemark(e.target.value)}
                  className="min-h-[80px] bg-background text-sm resize-none focus-visible:ring-1"
                />
                <div className="flex justify-end">
                  <Button onClick={handleAddRemark} disabled={!newRemark.trim()} size="sm" className="px-4">
                    <Plus className="h-3 w-3 mr-1.5" /> Add Remark
                  </Button>
                </div>
              </div>
            </div>
          </section>

          {/* Section: Activity Log */}
          <section className="space-y-4">
            <div className="flex items-center gap-2 text-primary/80 font-medium pb-2 border-b border-border/50">
              <History className="h-4 w-4" />
              <h3>Activity Log</h3>
            </div>

            <div className="relative pl-2">
              <div className="absolute left-[11px] top-2 bottom-2 w-px bg-border/60" />
              <div className="space-y-6">
                {record.activityLog.slice().reverse().map((entry) => (
                  <div key={entry.id} className="relative pl-8">
                    <div className="absolute left-0 top-1 w-6 h-6 rounded-full bg-background border border-border flex items-center justify-center z-10">
                      <div className="w-1.5 h-1.5 rounded-full bg-primary" />
                    </div>
                    <div>
                      <div className="flex justify-between items-start w-full gap-4">
                        <p className="text-sm font-medium text-foreground">{entry.action}</p>
                        <Button
                          size="icon"
                          variant="ghost"
                          className="h-4 w-4 text-muted-foreground hover:text-destructive -mt-1"
                          onClick={() => deleteActivityLog(record.id, entry.id)}
                        >
                          <Trash2 className="h-3 w-3" />
                        </Button>
                      </div>
                      <p className="text-xs text-muted-foreground mt-0.5">{entry.details}</p>
                      <p className="text-[10px] text-muted-foreground mt-1 font-mono opacity-70">
                        {new Date(entry.timestamp).toLocaleString()} â€¢ {entry.user}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </section>

        </div>

        {/* Footer - Fixed */}
        <div className="p-4 border-t border-border bg-card z-10 flex justify-end">
          <Button onClick={onClose} className="px-8">
            Update
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}
