"use client"

import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Phone, MessageCircle, Eye, ArrowRight } from "lucide-react"
import { format } from "date-fns"
import type { CRMRecord } from "@/lib/types"
import { cn } from "@/lib/utils"

interface RecentActivityTableProps {
    records: CRMRecord[]
    onView: (record: CRMRecord) => void
}

export function RecentActivityTable({ records, onView }: RecentActivityTableProps) {
    // Take latest 10 records
    // Take latest 10 records sorted by upload time
    const recentRecords = [...records]
        .sort((a, b) => new Date(b.uploadedAt).getTime() - new Date(a.uploadedAt).getTime())
        .slice(0, 10)

    const totalProtect = recentRecords.filter(r => r.type === "protect").length
    const totalSettlement = recentRecords.filter(r => r.type === "settlement").length

    return (
        <div className="space-y-4">
            <div className="flex items-center justify-between">
                <h3 className="text-lg font-semibold">Recent Activity</h3>
                <Button variant="link" className="text-sm h-auto p-0">
                    View all <ArrowRight className="ml-1 h-3 w-3" />
                </Button>
            </div>

            <div className="border rounded-md bg-card">
                <Table className="table-fixed">
                    <TableHeader>
                        <TableRow>
                            <TableHead className="w-[100px]">Date</TableHead>
                            <TableHead>Name</TableHead>
                            <TableHead className="w-[120px]">Mobile</TableHead>
                            <TableHead className="w-[100px]">Type</TableHead>
                            <TableHead className="w-[120px]">Source</TableHead>
                            <TableHead className="w-[100px]">Status</TableHead>
                            <TableHead className="text-center w-[100px]">Actions</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {recentRecords.length === 0 ? (
                            <TableRow>
                                <TableCell colSpan={7} className="h-24 text-center text-muted-foreground">
                                    No recent activity found.
                                </TableCell>
                            </TableRow>
                        ) : (
                            recentRecords.map((record) => (
                                <TableRow key={record.id} className="h-12">
                                    <TableCell className="whitespace-nowrap py-3">
                                        {format(new Date(record.formFilledDate), "MMM d")}
                                    </TableCell>
                                    <TableCell className="font-medium py-3">{record.name}</TableCell>
                                    <TableCell className="py-3">{record.mobileNumber}</TableCell>
                                    <TableCell className="py-3">
                                        <Badge
                                            variant="outline"
                                            className={cn(
                                                "capitalize",
                                                record.type === "protect"
                                                    ? "border-blue-200 bg-blue-50 text-blue-700 dark:bg-blue-900/20 dark:text-blue-400"
                                                    : "border-orange-200 bg-orange-50 text-orange-700 dark:bg-orange-900/20 dark:text-orange-400"
                                            )}
                                        >
                                            {record.type}
                                        </Badge>
                                    </TableCell>
                                    <TableCell className="py-3">
                                        <div className="flex items-center gap-2">
                                            <span className={cn(
                                                "h-2 w-2 rounded-full",
                                                record.partner === "sayyam" ? "bg-purple-500" : "bg-indigo-500"
                                            )} />
                                            <span className="capitalize">{record.partner}</span>
                                        </div>
                                    </TableCell>
                                    <TableCell className="py-3">
                                        <Badge variant="secondary" className="capitalize font-normal">
                                            {record.status}
                                        </Badge>
                                    </TableCell>
                                    <TableCell className="text-center py-3">
                                        <div className="flex items-center justify-center gap-1">
                                            <Button variant="ghost" size="icon" className="h-8 w-8 text-green-600 hover:text-green-700 hover:bg-green-50">
                                                <Phone className="h-4 w-4" />
                                            </Button>
                                            <Button variant="ghost" size="icon" className="h-8 w-8 text-green-600 hover:text-green-700 hover:bg-green-50">
                                                <MessageCircle className="h-4 w-4" />
                                            </Button>
                                            <Button variant="ghost" size="icon" className="h-8 w-8" onClick={() => onView(record)}>
                                                <Eye className="h-4 w-4" />
                                            </Button>
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ))
                        )}

                        {/* Totals Row */}
                        {recentRecords.length > 0 && (
                            <TableRow className="bg-muted/50 font-medium">
                                <TableCell colSpan={3}>Total in view</TableCell>
                                <TableCell>
                                    <div className="flex gap-3 text-xs">
                                        <span className="text-blue-600 dark:text-blue-400">{totalProtect} Protect</span>
                                        <span className="text-orange-600 dark:text-orange-400">{totalSettlement} Settlement</span>
                                    </div>
                                </TableCell>
                                <TableCell colSpan={3} />
                            </TableRow>
                        )}
                    </TableBody>
                </Table>
            </div>
        </div>
    )
}
